import React, { Component } from 'react'
import {Card,Button,Modal,message} from 'antd'
import {parseUrlSearch} from '../util'
import {withRouter} from 'react-router-dom'
import {getAction} from '@/axios';
import PreviewDoc from '@/pages/common/PreviewDoc';
class Index extends Component {
    state={
        id:''
    }
    componentDidMount() {
        let param=parseUrlSearch()
        this.setState({
            type:param.type,
            id:param.id || ''
        })
    }
    handleFinish=()=>{
        Modal.confirm({
            title:'提交确认',
            content:'请确认是否完成并提交？',
            okText:'确认',
            cancelText:'取消',
            onOk:()=>{
                getAction(`/ybt/form/task/lauch/submit`,{
                    formTaskId:this.state.id
                }).then(res=>{
                    if(res.success){
                        console.log(res)
                        Modal.success({
                            title:'填报已完成',
                            content:'请前往我的填报任务查看进度',
                            okText:'确定',
                            onOk:()=>{
                                this.props.history.push('/myFillingTask')
                            }
                        })
                    }else{
                        message.error(res.obj)
                    }
                })
            }
        })
    }
    render() {
        return (
            <div style={{padding:20}}>
                <Card title={'填报预览'} style={{height:"calc(100vh - 170px)"}} bodyStyle={{height:"calc(100vh - 225px)",padding:0}}>
                    {
                        this.state.id && <PreviewDoc id={this.state.id} type='formTaskId'/>
                    }
                    {/* <iframe scrolling='auto' title='预览' style={{border:'none',width:'100%',height:"calc(100vh - 270px)"}} src={`http://${window.location.host}/ybt/form/dl/result/view?formTaskId=${this.state.id}`}></iframe> */}
                </Card>
                <div style={{marginTop:10,textAlign:'right'}}>
                    <Button onClick={()=>{this.props.history.push(`/myFillingTask/step2?id=${this.state.id}&type=fill`)}} type='primary' ghost style={{ marginRight: 8 }}>上一步</Button>
                    <Button  onClick={()=>{this.handleFinish()}} type='primary' style={{ marginRight: 8 }}>完成</Button>
                    <Button onClick={()=>{this.props.history.push(`/myFillingTask`)}} style={{ marginRight: 40 }}>取消</Button>
                </div>
            </div>
        )
    }
}

export default withRouter(Index)